a o sensible de acuerdo al nivel requerido y autorizado. ")])]),_c('div',{staticClass:"tarjeta color-acento-contenido p-3",attrs:{"x":"77%","y":"86%","numero":"+"}},[_c('p',{staticClass:"mb-0"},[_vm._v("Generar un esquema de inicio y enfoque del dato permitirá la objetividad de la información que contiene; no obstante, la vida útil del dato es de carácter trascendental para la oportunidad de la información. Por consiguiente, para su almacenamiento se debe contar con criterios de conservación, respaldo y evaluación del momento de eliminación bajo términos, protocolos y criterios establecidos. ")])])])],1)])])],1)
